.nav-cart-btn {
  margin-left: auto;
  position: relative;
}

.nav-cart-btn-counter {
  position: absolute;
  font-size: 0.7em;
  top: -4px;
  right: 2px;
  background-color: hsl(26, 100%, 55%);
  padding: 2px 6px;
  border-radius: 7px;
  font-weight: bold;
  color: white;
  border: none;
}
